import React from 'react';

const ProjectCard = ({
  project: {
    headline,
    subtitles,
    description,
    tags,
    details,
    imgSrc,
    sourceLink,
    demoLink,
  },
  setCurrentCb,
  showModalCb,
}) => (
  <div
    className="flex flex-col rounded-lg dark:bg-dark-card-hover my-4 shadow-md border dark:border-dark-bg border-t-0 md:w-80 lg:w-96 md:mr-4"
  >
    <img src={imgSrc} alt={headline} className="rounded-lg w-full h-48 object-cover" />
    <div className="text-base dark:text-dark-text-primary flex flex-row flex-wrap justify-center mt-4 mb-1">
      {subtitles.map((sub, i) => (
        <span
          key={`sub-${i}`}
          className="flex flex-row items-center dark:text-dark-text-secondary"
        >
          <p className="w-fit">{sub}</p>
          {i !== 2 && (
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="8"
            height="8"
            version="1.1"
            className="mx-2"
          >
            <circle cx="4" cy="4" r="4" fill="gray" />
          </svg>
          )}
        </span>
      ))}
    </div>
    <p className="self-center text-xl">{headline}</p>
    <button
      className="btn mt-6 mb-4 self-center"
      type="button"
      onClick={() => {
        setCurrentCb({
          headline,
          subtitles,
          description,
          tags,
          details,
          imgSrc,
          sourceLink,
          demoLink,
        });
        showModalCb(true);
      }}
    >
      View Project
    </button>
  </div>
);

export default ProjectCard;
