import React, { useEffect, useState } from 'react';
import Flickity from 'react-flickity-component';
import '../style/flickity.css';
import ProjectCard from './ProjectCard';
import projects from '../constants/projects';
import Modal from './Modal';

const Slider = () => {
  const [showModal, setShowModal] = useState(false);
  const [current, setCurrent] = useState(null);

  useEffect(() => {
    if (showModal === true) {
      document.body.style.overflow = 'hidden';
    } else if (showModal === false) {
      document.body.style.overflow = 'unset';
    }
  }, [showModal]);

  return (
    <>
      {showModal && current !== null && (
        <Modal project={current} closeModalCallBack={setShowModal} />
      )}

      <Flickity
        options={{
          initialIndex: 1,
          freeScroll: true,
          wrapAround: true,
        }}
      >
        {projects.map(
          (
            {
              headline,
              subtitles,
              description,
              tags,
              details,
              imgSrc,
              sourceLink,
              demoLink,
            },
            i,
          ) => (
            <ProjectCard
              project={{
                headline,
                subtitles,
                description,
                tags,
                details,
                imgSrc,
                sourceLink,
                demoLink,
              }}
              key={`project-${i}`}
              setCurrentCb={setCurrent}
              showModalCb={setShowModal}
            />
          ),
        )}
      </Flickity>
    </>
  );
};

export default Slider;
