import React, { useEffect, useState } from 'react';
import menu from '../assets/hamburger.svg';
import cross from '../assets/cross-menu.svg';

const NavBar = ({ dark, setDarkCb }) => {
  const [overlay, showOverlay] = useState(false);
  const [show, setShow] = useState(false);

  const links = ['About', 'Experience', 'Projects', 'Contact'];

  useEffect(() => {
    setTimeout(() => setShow(overlay), 300);
    if (overlay === true) {
      document.body.style.overflow = 'hidden';
    } else if (overlay === false) {
      document.body.style.overflow = 'unset';
    }
  }, [overlay]);

  const scrollDesktop = (id) => {
    const yOffset = -100;
    const element = document.getElementById(id);
    const y = element.getBoundingClientRect().top + window.scrollY + yOffset;

    window.scrollTo({ top: y, behavior: 'smooth' });
  };

  const scrollMobile = (id) => {
    const yOffset = -50;
    const element = document.getElementById(id);
    const y = element.getBoundingClientRect().top + window.scrollY + yOffset;

    window.scrollTo({ top: y, behavior: 'smooth' });
  };

  return (
    <header className="fixed top-0 w-full bg-white dark:bg-dark-bg z-50">
      <div className="px-4 py-2 md:px-6 md:pt-10">
        <div className="flex flex-row items-center justify-between">
          <a
            href="#home"
            onClick={() => showOverlay(false)}
            className="text-xl text-primary-blue dark:text-secondary-blue"
          >
            AT
          </a>
          <div className="md:hidden">
            {!show ? (
              <button type="button" onClick={() => showOverlay(true)}>
                <img
                  alt="menu"
                  src={menu}
                  width="30"
                  className={dark ? 'svg-dark-filter' : 'svg-light-filter'}
                />
              </button>
            ) : (
              <button
                type="button"
                className="self-end"
                onClick={() => showOverlay(false)}
              >
                <img
                  src={cross}
                  alt="close"
                  className={`h-[30px] w-[30px] ${
                    dark ? 'svg-dark-filter' : 'svg-light-filter'
                  }`}
                />
              </button>
            )}
          </div>
          <div className="hidden md:flex flex-row">
            {links.map((path, i) => (
              <button
                key={`path-md-${i}`}
                className="uppercase mx-4 dark:text-dark-text-primary hover:text-primary-blue dark:hover:text-secondary-blue transition-colors"
                onClick={() => scrollDesktop(path.toLowerCase())}
                type="button"
              >
                {path}
              </button>
            ))}
          </div>
        </div>
        <div className="hidden md:flex z-50 justify-end pe-4 pt-2">
          <label
            htmlFor="dark-mode"
            className="relative inline-flex items-center cursor-pointer text-primary-blue dark:text-secondary-blue"
          >
            <input
              id="dark-mode"
              type="checkbox"
              value={dark}
              checked={dark}
              onChange={() => setDarkCb()}
              className="sr-only peer"
            />
            <div className="w-11 h-6 bg-gray-200 peer-focus:outline-none peer-focus:ring-4 peer-focus:ring-dark-text-primary dark:peer-focus:ring-secondary-blue rounded-full peer dark:bg-gray-700 peer-checked:after:translate-x-full peer-checked:after:border-white after:content-[''] after:absolute after:top-[2px] after:left-[2px] after:bg-dark-text-primary dark:after:bg-dark-bg after:border-gray-300 after:border after:rounded-full after:h-5 after:w-5 after:transition-all dark:border-gray-600 peer-checked:bg-secondary-blue" />
            <span className="ml-3 text-base font-light">Dark mode</span>
          </label>
        </div>
      </div>
      <div
        className={`absolute w-full bg-white dark:bg-dark-bg ${
          overlay ? 'h-screen' : 'h-0'
        } transition-[height] duration-300 ease-in-out z-50 md:hidden`}
      >
        <div className="flex flex-col items-center text-black dark:text-secondary-blue">
          {overlay && show && (
            <>
              {links.map((path, i) => (
                <button
                  key={`path-sm-${i}`}
                  className="uppercase my-4"
                  onClick={() => {
                    scrollMobile(path.toLowerCase());
                    showOverlay(false);
                  }}
                  type="button"
                >
                  {path}
                </button>
              ))}

              <label
                htmlFor="dark-mode"
                className="relative inline-flex items-center cursor-pointer my-9"
              >
                <input
                  id="dark-mode"
                  type="checkbox"
                  value={dark}
                  checked={dark}
                  onChange={() => setDarkCb()}
                  className="sr-only peer"
                />
                <div className="w-11 h-6 bg-gray-200 peer-focus:outline-none peer-focus:ring-4 peer-focus:ring-dark-text-primary dark:peer-focus:ring-secondary-blue rounded-full peer dark:bg-gray-700 peer-checked:after:translate-x-full peer-checked:after:border-white after:content-[''] after:absolute after:top-[2px] after:left-[2px] after:bg-dark-text-primary dark:after:bg-dark-bg after:border-gray-300 after:border after:rounded-full after:h-5 after:w-5 after:transition-all dark:border-gray-600 peer-checked:bg-secondary-blue" />
                <span className="ml-3 text-base font-light">Dark mode</span>
              </label>
            </>
          )}
        </div>
      </div>
    </header>
  );
};

export default NavBar;
