import { useEffect, useState } from 'react';
import Logos from './components/Logos';
import Experience from './components/Experience';
import Main from './components/Main';
import Projects from './components/Projects';
import Contact from './components/Contact';
import Footer from './components/Footer';
import NavBar from './components/NavBar';
import about from './constants/about';
import Slider from './components/Slider';
import useWindowDimensions from './hooks/useWindowDimensions';

const App = () => {
  const [darkBool, setDarkBool] = useState(false);
  const [isSmallScreen, setIsSmallScreen] = useState(true);

  const { width } = useWindowDimensions();

  useEffect(() => {
    const dark = localStorage.getItem('dark');
    if (dark === 'true') {
      setDarkBool(true);
    }
  }, []);

  useEffect(() => {
    if (width >= 768) {
      setIsSmallScreen(false);
    } else {
      setIsSmallScreen(true);
    }
  }, [width]);

  const setDark = () => {
    localStorage.setItem('dark', !darkBool);
    setDarkBool(!darkBool);
  };

  return (
    <div className={`font-ubuntu ${darkBool && 'dark'}`}>
      <div className="dark:bg-dark-bg dark:text-dark-text-primary">
        <NavBar dark={darkBool} setDarkCb={setDark} />
        <div className="container px-4 overflow-y-auto md:px-16 lg:px-32">
          <section className="my-6 pt-8 md:pt-24 md:mb-12" id="home">
            <Main />
          </section>
          <section className="flex flex-col my-6 md:my-12" id="about">
            <p className="text-2xl md:text-4xl font-bold text-primary-blue my-1 dark:text-inherit">About</p>
            <p className="text-base md:text-lg my-2 whitespace-pre-wrap">{`${about}`}</p>
          </section>
          <section className="my-6 md:my-12">
            <Logos />
          </section>
          <section className="flex flex-col my-6 md:my-12" id="experience">
            <p className="text-2xl md:text-4xl font-bold text-primary-blue my-1 dark:text-inherit">Experience</p>
            <Experience />
          </section>
          <section className="flex flex-col my-6 md:my-12" id="projects">
            <p className="text-2xl md:text-4xl font-bold text-primary-blue my-1 dark:text-inherit">Projects</p>
            {isSmallScreen ? <Projects /> : <Slider />}
          </section>
          <section className="flex flex-col my-6" id="contact">
            <p className="text-2xl font-bold text-primary-blue my-1 dark:text-inherit">Contact</p>
            <Contact />
          </section>
          <section className="mt-10">
            <Footer dark={darkBool} />
          </section>
        </div>
      </div>

    </div>
  );
};

export default App;
